@font-face {
  font-family: "OpenSans";
  font-display: swap;
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2") url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  font-display: swap;
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2") url("../fonts/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
html {
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

a:visited {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

button {
  outline: transparent;
  border: none;
  padding: 0;
  font-family: inherit;
  color: inherit;
  background: transparent;
  cursor: pointer;
}

input {
  outline: transparent;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
  padding: 0;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  margin: 0;
  height: 100%;
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 1.2;
  letter-spacing: auto;
  color: #363636;
  background-color: #fff;
}
body.js-overflow {
  overflow: hidden;
}

.site-container {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100%;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
  max-width: 1210px;
}

.title-h1 {
  font-weight: 700;
  font-size: 36px;
  line-height: 1.2;
}
@media (max-width: 991.98px) {
  .title-h1 {
    font-size: 26px;
  }
}

.title-h2 {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
}

.title-h3 {
  font-weight: 800;
  font-size: 16px;
}

.js-locked {
  overflow: hidden;
}

.error {
  border: 1px solid rgba(255, 0, 0, 0.8) !important;
}

.err-animated {
  transition: all 0.3s;
  animation: err 0.4s;
}

.valid {
  border: 1px solid rgba(0, 128, 0, 0.8) !important;
}

.js-scroll {
  opacity: 0;
  transition: opacity 0.5s;
}
.js-scroll.scrolled {
  opacity: 1;
}
.js-scroll.scrolled.slide-left {
  animation: slide-in-left 1s ease-in-out both;
}
.js-scroll.scrolled.slide-right {
  animation: slide-in-right 1s ease-in-out both;
}
.js-scroll.scrolled.fade-in {
  animation: fade-in 1s ease-in-out both;
}
.js-scroll.scrolled.fade-in-bottom {
  animation: fade-in-bottom 1s ease-in-out both;
}

@keyframes slide-in-left {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slide-in-right {
  0% {
    opacity: 0;
    transform: translateX(100px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fade-in-bottom {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes err {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0);
  }
}
.header {
  padding: 36px 0 43px;
}
.header__title {
  text-align: center;
  color: #45852d;
}

.filter {
  padding-bottom: 45px;
}
@media (max-width: 600px) {
  .filter__label {
    display: flex;
    flex-direction: column;
  }
}
.filter__label-text {
  margin-right: 12px;
  font-weight: 700;
}
@media (max-width: 650px) {
  .filter__label-text {
    margin: 0 0 15px;
  }
}
.filter__input {
  height: 50px;
  border: 1px solid #d8d8d8;
  border-radius: 25px;
  max-width: 418px;
  width: 100%;
  padding: 0 25px;
}

.houses {
  padding-bottom: 77px;
}
.houses__list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -22px;
  margin-bottom: 60px;
}
.houses__house {
  display: block;
  flex-basis: calc((100% - 66px) / 3);
  margin: 0 0 38px 22px;
  transition: all 0.3s ease-in-out;
}
.houses__house:hover {
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
}
@media (max-width: 1200px) {
  .houses__house {
    flex-basis: calc((100% - 44px) / 2);
  }
}
@media (max-width: 650px) {
  .houses__house {
    flex-basis: 100%;
  }
}
.houses__link-wrapper {
  text-align: center;
}
.houses__link {
  display: inline-block;
  min-width: 150px;
  padding: 12px 10px;
  border-radius: 25px;
  border: 1px solid #d8d8d8;
  transition: all 0.3s;
}
.houses__link span {
  position: relative;
  padding-right: 15px;
}
.houses__link span::after {
  content: "";
  width: 10px;
  height: 15px;
  top: 1px;
  right: 0;
  position: absolute;
  background-image: url("../images/svgSprite.svg#Chevron");
  background-position: 0 0;
  background-size: cover;
  background-repeat: no-repeat;
  transition: all 0.3s ease-in-out;
  transform: translateX(0);
}
.houses__link:hover {
  border-color: #000;
}
.houses__link:hover span::after {
  transform: translateX(10px);
}

.house-card {
  background-color: #f8f8f8;
  border: 1px solid #d8d8d8;
  min-height: 100%;
}
.house-card__figure {
  position: relative;
  margin: 0;
  width: 100%;
  height: 277px;
}
.house-card__figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.house-card__label {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 8px 10px;
  color: #fff;
  background-color: #006f79;
}
.house-card__label--orange {
  background-color: #ec6608;
}
.house-card__label-orange {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 8px 10px;
  color: #fff;
  background-color: #ec6608;
}
.house-card__body {
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  margin-bottom: 10px;
  padding: 20px 20px 17px;
}
.house-card__wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.house-card__content {
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  min-height: 100%;
}
.house-card__title {
  flex-grow: 1;
  margin-bottom: 5px;
}
.house-card__address {
  margin-bottom: 15px;
}
.house-card__footer {
  flex: 0 0 auto;
  padding: 0 20px 17px;
}